@import "../vars";

#clockdiv{
  display: inline-block;
  margin-bottom: 20px;
  //font-family: sans-serif;
  //font-size: 30px;
  //font-weight: 100;
  text-align: center;
  color: #fff;
  user-select: none;
}

#clockdiv > div {
  position: relative;
  display: inline-block;
  padding: 10px;
  border-radius: 3px;
  //background: $d-purple;
  &:not(:last-child) {
    &:before {
      content: ':';
      position: absolute;
      top: 34%;
      right: -10px;
      font-family: $alvira-bold;
      font-size: 34px;
      line-height: 1;
      transform: translate(0, -50%);
    }
  }
}

#clockdiv div > span{
  display: inline-block;
  padding: 16px 14px;
  border-radius: 15px;
  background: $d-purple;
  font-family: $alvira-bold;
  font-size: 45px;
  line-height: 1.1;
  box-shadow: 0px 0px 20px #7900B4;
}

.smalltext{
  padding-top: 2px;
  font-family: $default-font;
  font-size: 12px;
  font-weight: 500;
}
