/*--------------------------------------------------------------
# Container Styles
--------------------------------------------------------------*/


section {
  position: relative;
  .cn {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
  }
}

.log-in {
  background: url(../img/back/BG.png) no-repeat center;
  //-webkit-background-size: cover;
  background-size: cover;
  background-color: #8301dc;
  @media only screen and (min-width: 992px) {
    background-size: 100% 100%;
  }
  &-cn {
    min-height: 100vh;
    padding-bottom: 10.7vw;
    align-items: center;
    justify-content: center;
  }
  &__logo {
    max-width: 60%;
    margin: 0 auto;
    @media only screen and (min-width: 641px) {
      max-width: 100%;
    }
  }
  &__title {
    margin: 32px auto 50px;
    font-size: 25px;
    font-weight: 500;
    line-height: 130%;
    text-align: center;
    color: #FFFFFF;
    @media only screen and (min-width: 480px) {
      font-size: 30px;
    }
    @media only screen and (min-width: 992px) {
      font-size: 40px;
    }
  }
  .form {
    max-width: 275px;
    margin: 0 auto;
    &__title {
      margin-bottom: 20px;
      font-family: $alvira-bold;
      font-size: 18px;
      font-weight: 500;
      line-height: 130%;
      color: #ffffff;
      text-align: center;
    }
    &__label {
      margin-bottom: 10px;
    }
    &__control {
      background: #FFFFFF;
      box-shadow: 0px 0px 25px #E7B7FF;
      border-radius: 15px;
    }
    .btn {
      margin-top: 10px;
      padding-top: 15px;
      padding-bottom: 15px;
      width: 100%;
      font-size: 18px;
      font-weight: bold;
      opacity: 1;
      box-shadow: none!important;
    }
  }
}
