@import "../vars";

#wpadminbar {
	//visibility: hidden;
}

* {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;

	&:before, &:after {
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	&::-webkit-input-placeholder {
		color: rgba($d-purple, 0.5);
		opacity: 1;
	}
	&:-moz-placeholder {
		color: rgba($d-purple, 0.5);
		opacity: 1;
	}
	&::-moz-placeholder {
		color: rgba($d-purple, 0.5);
		opacity: 1;
	}

	&:-ms-input-placeholder {
		color: rgba($d-purple, 0.5);
		opacity: 1;
	}
	&::-webkit-scrollbar-track {
		-webkit-box-shadow: inset 0 0 6px rgba($l-purple, 0.7);
		background-color: rgba($l-purple, 0.1);
	}
	&::-webkit-scrollbar {
		width: 6px;
		background-color: rgba($l-purple, 0.1);
	}
	&::-webkit-scrollbar-thumb {
		background-color: $d-purple;
	}
}

@media only screen and (max-width: 992px) {
	*::-webkit-scrollbar-track {
		-webkit-box-shadow: inset 0 0 6px rgba(#999, 0.5);
		background-color: rgba($l-purple, 0.1);
	}

	*::-webkit-scrollbar {
		width: 6px;
		background-color: rgba($l-purple, 0.9);
	}

	*::-webkit-scrollbar-thumb {
		background-color: $d-purple;
	}
}

body input:focus:required:invalid,
body textarea:focus:required:invalid {
	border-color: red;
	color: orangered;
}

body input:required:valid,
body textarea:required:valid {
	color: #333;
}

html, body {
	margin: 0;
	padding: 0;
	width: 100%;
}

* {
	&:focus {
		outline: none;
	}
}

body {
	position: relative;
	display: block;
	min-width: 320px;
	background-color: #fefcff;
	font-family: $default-font;
	font-size: 16px;
	//line-height: 1.65;
	letter-spacing: 0.1px;
	//font-family: $default-font;
	color: #000;
	overflow-x: hidden;
}

::selection {
	background-color: $l-purple;
	color: #fff;
}

/*--------------------------------------------------------------
# Grid Styles
--------------------------------------------------------------*/

.wrapper {
	display: flex;
	flex-direction: column;
	height: auto;
	min-height: 100%;
	overflow-x: hidden;
	@media only screen and (min-width: 992px) {
		min-height: 100vh;
	}
}


.main {
	position: relative;
	flex: 1 0 auto;
	height: 100%;
}

.footer {
	flex: 0 0 auto
}

.cn {
	position: relative;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	width: 100%;
	max-width: 100%;
	margin: 0 auto;
	padding: 0 15px;
}

.cn-fl {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	width: 100%;
	max-width: 100%;
}

.col-6 {
	justify-content: space-between;
	flex-direction: row;
	flex-wrap: wrap;
	.item {
		padding: 5px 10px;
		width: 100%;
	}
}
.col-4 {
	display: flex;
	justify-content: space-between;
	flex-direction: row;
	flex-wrap: wrap;
	.item {
		padding: 25px;
		width: 100%;
	}
}
.col-3 {
	 display: flex;
	 justify-content: space-between;
	 flex-direction: row;
	 flex-wrap: wrap;
	 .item {
		 padding: 25px;
		 width: 100%;
	 }
 }


iframe {
	margin: 0 auto;
	max-width: 100%;
}


section {
	position: relative;
	width: 100%;
}

.main {
	position: relative;
}


.error-404 {
	text-align: center;
	.page-content {
		p {
			color: $blue-dark;
			font-weight: bold;
		}
	}
	&-img {
		display: inline-block;
		max-width: 300px;
		margin: 40px auto 70px;
	}
}
.error404 {
	.choose-s__items>a {
		padding: 29px 26px;
	}
}


/*--------------------------------------------------------------
# Other blocks Styles
--------------------------------------------------------------*/
.img-responsive {
	display: block;
	max-width: 100%;
	height: auto;
}

img {
	max-width: 100%;
}


.hidden {
	display: none;
}

