@import "../vars";

a {
  color: $d-orange;
  text-decoration: underline;
}

/*--------------------------------------------------------------
# Typography Styles
--------------------------------------------------------------*/
h1, .h1 {
  position: relative;
  display: block;
  width: 100%;

  font-family: $alvira-bold;
  font-size: 48px;
  //font-weight: bold;
}

h2, .h2 {
  font-family: $alvira-bold;
  font-size: 30px;
  //font-weight: 300;
  line-height: 1.2;
  @media only screen and (min-width: 480px) {
    font-size: 36px;
  }
  @media only screen and (min-width: 768px) {
    font-size: 50px;
  }
}

h3, .h3 {
  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;
}

p {
  margin-top: 0;
}

blockquote {
  position: relative;
  margin: 0;
  padding: 20px;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  &:before {
    content: '“';
    position: absolute;
    left: 0;
    top: 19px;
    display: block;
    font-size: 54px;
    //line-height: 63px;
    text-align: center;
    color: $blue;
  }
  &:after {
    content: '”';
    position: absolute;
    right: 0;
    bottom: -19px;
    display: block;
    font-size: 54px;
    //line-height: 63px;
    text-align: center;
    color: $blue;
  }
}

.s {
  &-title {
    width: 100%;
    font-family: $alvira-bold;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
    span {
      color: $d-orange;
    }
  }
  &-sub-title {
    width: 100%;
    margin-bottom: 40px;
    text-align: center;
  }
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
  display: inline;
  float: left;
  margin-right: 2.5em;
  margin-bottom: 2.5em;
}

.alignright {
  display: inline;
  float: right;
  margin-left: 2.5em;
  margin-bottom: 2.5em;
}

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
}
