input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
	-webkit-backface-visibility: hidden;

	box-sizing: border-box;
	outline: none;
	//padding: #{.36 * $size__spacing-unit} #{.66 * $size__spacing-unit};
	-webkit-appearance: none;
	outline-offset: 0;
	//border-radius: 0;

	&:focus {
		//border-color: $color__link;
		//outline: thin solid rgba( $color__link, 0.15 );
		outline-offset: -4px;
	}
}


.form__label {
	position: relative;
	display: block;
	margin: 0 0 20px;

	font-size: 18px;
	font-weight: bold;
	line-height: 23px;
	text-align: left;
	color: #3E4148;
	opacity: 1;
	transition: .333s ease top, .333s ease opacity;
	& textarea {
		height: 92px;
	}
	span {
		display: block;
		margin-bottom: 7px;
	}
}
.label-pr {
	padding-right: 20px;
}



.form__control {
	width: 100%;
	max-width: 100%;
	height: auto;
	padding: 17px 23px;
	border-radius: 13px;
	border: 1px solid #FC6763;

	background: #FFFFFF;
	font-size: 15px;
	color: #3E4148;
	line-height: 1;
	&:focus {
		border: 1px solid $l-purple;
		outline-color: transparent;
	}
	//&::-webkit-input-placeholder {
	//	color: #85929F;
	//
	//}
	//
	//&:-moz-placeholder {
	//	color: #85929F;
	//}
	//
	//&::-moz-placeholder {
	//	color: #85929F;
	//	//opacity: 0.5;
	//}
	//
	//&:-ms-input-placeholder {
	//	color: #85929F;
	//	//opacity: 0.5;
	//}
	&.error {
		border-color: red;
	}
}

.wpcf7-form-control-wrap {
	display: block;
	width: 100%;
}

.form {
	&__title {
		text-align: center;
		+ p {
			display: flex;
			label {

			}
		}
	}
	&__box {
		max-width: 690px;
		margin: 40px auto 0;
		.form__label {
			display: inline-block;
			vertical-align: top;
			&:first-child {
				flex: 1 1 auto;
				padding-right: 7px;
			}
			&:last-child {
				flex: 0 0 210px;
				.brn {
					width: 100%;
				}
			}
		}

		&--full {
			.form__title {
				text-align: left;
				& + p {
					flex-direction: column;
				}
			}
			.form__label {
				width: 100%;
				max-width: 290px;
				&:first-child {
					padding-right: 0;
				}
				&:last-child {
					flex: 1 1 auto;
				}
			}
		}
	}

}