/*--------------------------------------------------------------
# BTN Styles
--------------------------------------------------------------*/

.btn {
	display: inline-block;
	vertical-align: baseline;
	padding: 20px 40px;
	border: none;
	border-radius: 51px;
	border-color: transparent;

	font-family: $alvira-bold;
	//font-weight: 500;
	text-decoration: none;
	cursor: pointer;
	text-align: center;
	text-transform: uppercase;
	transition: all 200ms ease-in;

	&--orange {
		min-width: 210px;
		color: #ffffff;
		background: linear-gradient(180deg, #FEBA36 0%, #F92387 163.64%);
		box-shadow: 0 1px 0 #ccc;
		opacity: 0.8;
		transition: all 200ms ease-in;
		&:hover {
			opacity: 1;
		}
	}

}

.btn-group {
	display: block;
	&--r {
		text-align: right
	}
	&--c {
		text-align: center
	}
	.btn:not(:last-child) {
		margin-right: 30px
	}
}
