@import "../_mixins/font-face";

@import url('https://fonts.googleapis.com/css?family=Montserrat:300,400,500,600,700,900&display=swap&subset=cyrillic');


@font-face {
  font-display: swap;
  font-family: 'Avenir LT 55 Roman';
  src: url('../fonts/AvenirLT-Roman.eot');
  src: local('Avenir LT 55 Roman'), local('AvenirLT-Roman'),
  url('../fonts/AvenirLT-Roman.eot?#iefix') format('embedded-opentype'),
  url('../fonts/AvenirLT-Roman.woff') format('woff'),
  url('../fonts/AvenirLT-Roman.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}


