/*--------------------------------------------------------------
# MNU Styles
  --------------------------------------------------------------*/
.main-mnu {
  display: inline-block;
  vertical-align: middle;
  text-align: right; }
  .main-mnu ul {
    display: flex;
    justify-content: flex-end;
    align-items: stretch;
    height: 100%;
    margin: 0;
    padding: 0;
    list-style-type: none; }
  .main-mnu li {
    position: relative;
    display: inline-block; }
  .main-mnu > ul > li > a {
    position: relative;
    display: inline-block;
    margin: 14px 20px 12px;
    overflow: hidden;
    border-bottom: 2px solid transparent;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    color: #ffffff;
    text-transform: uppercase;
    text-decoration: none;
    transition: all 200ms ease-in-out; }
    .main-mnu > ul > li > a:hover {
      border-bottom: 2px solid #BF80ED; }
  .main-mnu > ul > li.current-menu-item a {
    border-bottom: 2px solid #BF80ED; }

#menu-main-menu {
  margin-right: -20px;
  margin-bottom: -8px; }

.toggle-mnu {
  position: relative;
  top: 20px;
  z-index: 10;
  transform: translate(0, -50%);
  display: inline-block;
  vertical-align: middle;
  width: 28px;
  height: 28px;
  margin-left: 15%; }
  .toggle-mnu.on {
    top: 22px !important; }
  .toggle-mnu span:after, .toggle-mnu span:before {
    content: "";
    position: absolute;
    left: 0;
    top: 9px;
    z-index: 4; }
  .toggle-mnu span:before {
    width: 75%; }
  .toggle-mnu span:after {
    top: 18px;
    width: 100%; }
  .toggle-mnu span {
    position: relative;
    display: block;
    width: 100%; }
  .toggle-mnu span, .toggle-mnu span:after, .toggle-mnu span:before {
    height: 2px;
    background-color: #fff;
    backface-visibility: hidden;
    border-radius: 2px;
    transition: all 0.3s; }
  .toggle-mnu.on span {
    background-color: transparent; }
  .toggle-mnu.on span:before {
    transform: rotate(45deg) translate(-1px, 0px);
    background-color: #fff; }
  .toggle-mnu.on span:after {
    transform: rotate(-45deg) translate(6px, -7px);
    background-color: #fff; }

/* The side navigation menu */
.sidenav {
  height: 100%;
  /* 100% Full-height */
  width: 0;
  /* 0 width - change this with JavaScript */
  position: fixed;
  /* Stay in place */
  z-index: 9999;
  /* Stay on top */
  top: 0;
  left: 0;
  background-color: #3F006C;
  overflow-x: hidden;
  /* Disable horizontal scroll */
  padding-top: 60px;
  /* Place content 60px from the top */
  transition: 0.5s;
  /* 0.5 second transition effect to slide in the sidenav */ }
  .sidenav .share {
    margin: 25px;
    text-align: center; }
    .sidenav .share a {
      display: inline-block;
      vertical-align: middle;
      margin: 0 15px;
      transition: all 20ms ease-in; }
      .sidenav .share a:hover {
        color: #F99D15; }

.hidden-mnu {
  width: 100%;
  max-width: 100%; }
  .hidden-mnu ul {
    width: 100%;
    flex-flow: column nowrap; }
  .hidden-mnu a {
    width: calc(100% - 2*20px); }

/* Position and style the close button (top right corner) */
.sidenav .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
  text-decoration: none; }

/* Style page content - use this if you want to push the page content to the right when you open the side navigation */
#pageWrap {
  min-width: 320px;
  transition: margin-left .5s; }
  #pageWrap:after {
    transition: all .5s;
    z-index: -1;
    opacity: 0; }
  #pageWrap.overlay:after {
    content: '';
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.4);
    opacity: 1;
    z-index: 1; }

/* On smaller screens, where height is less than 450px, change the style of the sidenav (less padding and a smaller font size) */
@media screen and (max-height: 450px) {
  .sidenav {
    padding-top: 15px; }
  .sidenav a {
    font-size: 18px; } }

