#clockdiv {
  display: inline-block;
  margin-bottom: 20px;
  text-align: center;
  color: #fff;
  user-select: none; }

#clockdiv > div {
  position: relative;
  display: inline-block;
  padding: 10px;
  border-radius: 3px; }
  #clockdiv > div:not(:last-child):before {
    content: ':';
    position: absolute;
    top: 34%;
    right: -10px;
    font-family: "Avenir LT 55 Roman", sans-serif;
    font-size: 34px;
    line-height: 1;
    transform: translate(0, -50%); }

#clockdiv div > span {
