.btn {
  display: inline-block;
  vertical-align: baseline;
  padding: 20px 40px;
  border: none;
  border-radius: 51px;
  border-color: transparent;
  font-family: "Avenir LT 55 Roman", sans-serif;
  text-decoration: none;
  cursor: pointer;
  text-align: center;
  text-transform: uppercase;
  transition: all 200ms ease-in; }
  .btn--orange {
    min-width: 210px;
    color: #ffffff;
    background: linear-gradient(180deg, #FEBA36 0%, #F92387 163.64%);
    box-shadow: 0 1px 0 #ccc;
    opacity: 0.8;
    transition: all 200ms ease-in; }
    .btn--orange:hover {
      opacity: 1; }

.btn-group {
  display: block; }
  .btn-group--r {
    text-align: right; }
  .btn-group--c {
    text-align: center; }
  .btn-group .btn:not(:last-child) {
    margin-right: 30px; }

